import { instance } from '@/utils/request'
const getSchool = (param, url = '?&s=App.Common_University.Search&app_key=F8AC731C0E75EE361397C9479CA4A443&return_data=0') => {
  let arr = []
  for (let [key, value] of Object.entries(param)) {
    let reg=/[u4E00-u9FA5]/g
    if (!reg.test(value)) {
      // 含有汉字
      value = encodeURIComponent(value)
    }
    arr.push(`${key}=${value}`)
    let str = arr.join('&')
    return instance.get(`${url}&${str}`)
  }
}

export {
  getSchool
}