<script>
import { ref, watch } from 'vue';
import list from '@/components/list'
import { Notify } from 'vant';
export default {
  components: {
    list,
  },
  setup() {
    const clist = ref(null);
    const fs = ref(null);
    const keyword = ref(null)
    const show1 = ref(false);
    const showList = ref(false);
    const showPopover = ref(false);
    const actions = [
      { text: '体育' },
      { text: '师范' },
      { text: '美术' },
    ];

    watch([show1, fs], ([newV1, newV2], [oldV1, oldV2]) => {
    })

    const confirm = () => {
      showList.value = true;
    }

    const cancel = () => {
      showList.value = false;
    }

    const search = () => {
      if (!fs.value) {
        Notify({ 
          type: 'warning', 
          message: '请输入分数',
          duration: 1000,
       });
       return
      }
      clist.value.onLoad()
    }

    const onSelect = (action, index) => {
      console.log(action)
      console.log(index)
    }

    return {
      fs,
      keyword,
      show1,
      showList,
      confirm,
      cancel,
      search,
      clist,
      showPopover,
      actions,
      onSelect,
    }
  }
}
</script>

