<template>
  <van-list
    v-model:loading="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onLoad"
  >
    <div class="item" v-for="item in list" :key="item.school_name">
      <div class="per">
        <span>校名：</span>
        <span>{{item.school_name}}</span>
      </div>
      <div class="per">
        <span>层次：</span>
        <span>{{item.school_level}}</span>
      </div>
      <div class="per">
        <span>所在地：</span>
        <span>{{item.school_province}} - {{item.school_city}}</span>
      </div>
      <div class="per">
        <span>详情：</span>
        <span class="btn" @click="showDetail(item)">查看详情</span>
      </div>
      <!-- <span class="btn" @click="showDetail(item)">查看详情</span> -->
    </div>
  </van-list>
</template>

<script>
import { ref, reactive, watch } from 'vue';
import { getSchool } from "@/utils/api"
import { Notify } from 'vant';
import { useRouter } from 'vue-router';

export default {
  props: {
    fs: {
      default: ''
    },
    keyword: {
      default: ''
    }
  },
  setup(props) {
    const router = useRouter()

    const list = ref([]);
    const loading = ref(false);
    const finished = ref(false);
    const pageData = reactive({
      count: 50,
      num: 1,
      total: 0
    })

    watch(() => props.keyword, (newV, oldV) => {
      debugger
      onLoad();
    })
    const onLoad = () => {
      if (!props.keyword) return
      let opt = {
        school_name: props.keyword,
        top_num: pageData.count
      }
      getSchool(opt).then(res => {
        if (res && res.ret == 200) {
          res.data.schools.forEach(s => {
            list.value.push(s);
          })
          loading.value = false;
          finished.value = true;
        }
      })
      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     list.value.push(list.value.length + 1);
      //   }
      //   // 加载状态结束
      //   loading.value = false;
      //   // 数据全部加载完成
      //   if (list.value.length >= 40) {
      //     finished.value = true;
      //   }
      // }, 1000);
    };

    const showDetail = (item) => {
      console.log(item.school_website)
      router.push({
        name: 'Detail',
        params: {
          url: item.school_website
        }
      })
    }

    return {
      list,
      onLoad,
      loading,
      finished,
      showDetail
    };
  },
};
// export default defineComponent({
//   setup() {
    
//   },
// })
</script>
<style lang="less">
.item {
  // box-sizing: content-box;
  // width: calc(100% - 20px);
  height: auto;
  background: white;
  border-radius: 5px;
  margin: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  .per {
    width: 100%;
    height: 30px;
  }
  .btn {
    padding: 10px 20px;
    color: rgba(25,137,250, 0.8);
  }:hover {
    background-color: rgba(25,137,250, 0.1)
  }
}
</style>